import { Version } from '@microsoft/sp-core-library';
import {
  type IPropertyPaneConfiguration,
  PropertyPaneTextField
} from '@microsoft/sp-property-pane';
import { BaseClientSideWebPart } from '@microsoft/sp-webpart-base';
import type { IReadonlyTheme } from '@microsoft/sp-component-base';
import { escape } from '@microsoft/sp-lodash-subset';

import styles from './HelloWorldWebPart.module.scss';
import * as strings from 'HelloWorldWebPartStrings';
import { TeamsService, IChannel, ITeamsServiceError } from './services/TeamsService';

export interface IHelloWorldWebPartProps {
  description: string;
}

export default class HelloWorldWebPart extends BaseClientSideWebPart<IHelloWorldWebPartProps> {

  private _isDarkTheme: boolean = false;
  private _environmentMessage: string = '';
  private _clickCount: number = 0;
  private _teamsService: TeamsService | null = null;
  private _channels: IChannel[] = [];
  private _selectedChannels: Set<string> = new Set();
  private _isLoading: boolean = false;
  private _error: string = '';
  private _channelFiles: { [channelId: string]: any[] } = {};

  public render(): void {
    this.domElement.innerHTML = `
    <section class="${styles.helloWorld} ${!!this.context.sdks.microsoftTeams ? styles.teams : ''}">
      <div class="${styles.headerCard}">
        <div class="${styles.welcome}">
          <img alt="" src="${this._isDarkTheme ? require('./assets/welcome-dark.png') : require('./assets/welcome-light.png')}" class="${styles.welcomeImage}" />
          <h2>Well done, ${escape(this.context.pageContext.user.displayName)}!</h2>
          <div class="${styles.environmentInfo}">${this._environmentMessage}</div>
          <div class="${styles.propertyInfo}">Web part property: <strong>${escape(this.properties.description)}</strong></div>
        </div>
      </div>

      <div class="${styles.teamsCard}">
        <h3> My Teams Channels</h3>
        <div class="${styles.teamsSection}">
          ${this._renderTeamsContent()}
        </div>
      </div>

      <div class="${styles.interactiveCard}">
        <h3> Interactive Test Area</h3>
        <div class="${styles.counterSection}">
          <p>Click counter: <span class="${styles.counterDisplay}">${this._clickCount}</span></p>
          <button class="${styles.primaryButton}" data-action="increment">Increment Counter</button>
          <button class="${styles.secondaryButton}" data-action="reset">Reset Counter</button>
        </div>
      </div>

      <div class="${styles.contentCard}">
        <h3> Welcome to SharePoint Framework!</h3>
        <p class="${styles.description}">
        The SharePoint Framework (SPFx) is an extensibility model for Microsoft Viva, Microsoft Teams and SharePoint. It's the easiest way to extend Microsoft 365 with automatic Single Sign On, automatic hosting and industry standard tooling.
        </p>
        <h4> Learn more about SPFx development:</h4>
          <ul class="${styles.links}">
            <li><a href="https://aka.ms/spfx" target="_blank"> SharePoint Framework Overview</a></li>
            <li><a href="https://aka.ms/spfx-yeoman-graph" target="_blank"> Use Microsoft Graph in your solution</a></li>
            <li><a href="https://aka.ms/spfx-yeoman-teams" target="_blank"> Build for Microsoft Teams using SharePoint Framework</a></li>
            <li><a href="https://aka.ms/spfx-yeoman-viva" target="_blank"> Build for Microsoft Viva Connections using SharePoint Framework</a></li>
            <li><a href="https://aka.ms/spfx-yeoman-store" target="_blank"> Publish SharePoint Framework applications to the marketplace</a></li>
            <li><a href="https://aka.ms/spfx-yeoman-api" target="_blank"> SharePoint Framework API reference</a></li>
            <li><a href="https://aka.ms/m365pnp" target="_blank"> Microsoft 365 Developer Community</a></li>
          </ul>
      </div>
    </section>`;

    this._bindEvents();
  }

  private _renderTeamsContent(): string {
    if (this._isLoading) {
      return `
        <div class="${styles.loadingState}">
          <div class="${styles.spinner}"></div>
          <p>Loading Teams channels...</p>
        </div>
      `;
    }

    if (this._error) {
      return `
        <div class="${styles.errorState}">
          <p class="${styles.errorMessage}"> ${this._error}</p>
          <button class="${styles.primaryButton}" data-action="retry">Try Again</button>
        </div>
      `;
    }

    if (this._channels.length === 0) {
      return `
        <div class="${styles.emptyState}">
          <button class="${styles.primaryButton}" data-action="load-channels">Load My Teams Channels</button>
        </div>
      `;
    }

    const groupedChannels = this._groupChannelsByTeam();
    let html = '<div class="${styles.channelsList}">';

    const teamNames = Object.keys(groupedChannels);
    for (let i = 0; i < teamNames.length; i++) {
      const teamName = teamNames[i];
      const channels = groupedChannels[teamName];

      html += `
        <div class="${styles.teamGroup}">
          <h4 class="${styles.teamName}"> ${escape(teamName)}</h4>
          <div class="${styles.channelsGroup}">
      `;

      for (let j = 0; j < channels.length; j++) {
        const channel = channels[j];
        const isChecked = this._selectedChannels.has(channel.id);
        const channelKey = `${channel.teamId}_${channel.id}`;
        const hasFiles = this._channelFiles[channelKey];

        html += `
          <div class="${styles.channelContainer}">
            <label class="${styles.channelItem}">
              <input type="checkbox"
                     data-channel-id="${channel.id}"
                     ${isChecked ? 'checked' : ''} />
              <span class="${styles.channelName}"># ${escape(channel.displayName)}</span>
              ${channel.description ? `<span class="${styles.channelDesc}">${escape(channel.description)}</span>` : ''}
              ${!hasFiles ? `<span class="${styles.showFilesBtn}" data-team-id="${channel.teamId}" data-channel-id="${channel.id}" data-action="show-files"></span>` : ''}
            </label>
            ${hasFiles ? `<div class="${styles.filesList}">${this._renderChannelFiles(channelKey)}</div>` : ''}
          </div>
        `;
      }

      html += `
          </div>
        </div>
      `;
    }

    html += '</div>';
    return html;
  }

  private _groupChannelsByTeam(): { [teamName: string]: IChannel[] } {
    return this._channels.reduce((groups, channel) => {
      const teamName = channel.teamName;
      if (!groups[teamName]) {
        groups[teamName] = [];
      }
      groups[teamName].push(channel);
      return groups;
    }, {} as { [teamName: string]: IChannel[] });
  }

  private _bindEvents(): void {
    this.domElement.addEventListener('click', (event: Event) => {
      const target = event.target as HTMLElement;
      const action = target.getAttribute('data-action');

      if (action === 'increment') {
        this._clickCount++;
        this._updateCounter();
      } else if (action === 'reset') {
        this._clickCount = 0;
        this._updateCounter();
      } else if (action === 'load-channels') {
        this._loadTeamsChannels();
      } else if (action === 'retry') {
        this._error = '';
        this._loadTeamsChannels();
      } else if (action === 'show-files') {
        const teamId = target.getAttribute('data-team-id');
        const channelId = target.getAttribute('data-channel-id');
        if (teamId && channelId) {
          this._loadChannelFiles(teamId, channelId);
        }
      } else if (action === 'open-folder') {
        const driveId = target.getAttribute('data-drive-id');
        const itemId = target.getAttribute('data-item-id');
        if (driveId && itemId) {
          this._loadFolderContents(driveId, itemId, target);
        }
      }
    });

    this.domElement.addEventListener('change', (event: Event) => {
      const target = event.target as HTMLInputElement;
      if (target.type === 'checkbox' && target.dataset.channelId) {
        this._handleChannelSelection(target.dataset.channelId, target.checked);
      }
    });
  }

  private _updateCounter(): void {
    const counterDisplay = this.domElement.querySelector(`.${styles.counterDisplay}`);
    if (counterDisplay) {
      counterDisplay.textContent = this._clickCount.toString();
    }
  }

  private async _loadTeamsChannels(): Promise<void> {
    this._isLoading = true;
    this._error = '';
    this.render();

    try {
      if (!this._teamsService) {
        const graphClient = await this.context.msGraphClientFactory.getClient('3');
        this._teamsService = new TeamsService(graphClient);
      }

      this._channels = await this._teamsService.getAllChannelsForUser();
      this._isLoading = false;
      this.render();
    } catch (error) {
      this._isLoading = false;
      const teamsError = error as ITeamsServiceError;

      switch (teamsError.type) {
        case 'NO_PERMISSIONS':
          this._error = 'Insufficient permissions. Please contact your administrator to approve Microsoft Graph permissions.';
          break;
        case 'NO_TEAMS':
          this._error = 'You are not a member of any teams.';
          break;
        case 'NETWORK_ERROR':
          this._error = 'Network error. Please check your connection and try again.';
          break;
        default:
          this._error = teamsError.message || 'An unexpected error occurred while loading channels.';
      }

      this.render();
      console.error('Error loading Teams channels:', error);
    }
  }

  private _handleChannelSelection(channelId: string, isSelected: boolean): void {
    if (isSelected) {
      this._selectedChannels.add(channelId);
    } else {
      this._selectedChannels.delete(channelId);
    }

    const selectedChannelsArray: string[] = [];
    this._selectedChannels.forEach(channelId => selectedChannelsArray.push(channelId));
    console.log('Selected channels:', selectedChannelsArray);
  }

  private async _loadChannelFiles(teamId: string, channelId: string): Promise<void> {
    const channelKey = `${teamId}_${channelId}`;

    try {
      if (!this._teamsService) {
        const graphClient = await this.context.msGraphClientFactory.getClient('3');
        this._teamsService = new TeamsService(graphClient);
      }

      const files = await this._teamsService.loadChannelFilesDirect(teamId, channelId);
      this._channelFiles[channelKey] = files;
      this.render();
    } catch (error) {
      console.error('Failed to load channel files:', error);
    }
  }

  private _renderChannelFiles(channelKey: string): string {
    const files = this._channelFiles[channelKey] || [];
    if (files.length === 0) return '<p>No files found</p>';

    let html = '<ul class="' + styles.filesUL + '">';
    files.forEach(file => {
      const icon = file.type === 'folder' ? '' : '';
      const folderId = `folder_${file.itemId}`;
      const folderContents = this._channelFiles[folderId];

      if (file.type === 'folder') {
        html += `<li class="${styles.folderItem}">
          <input type="checkbox" data-file-id="${file.id}" />
          <span class="${styles.folderName}" data-action="open-folder" data-drive-id="${file.driveId}" data-item-id="${file.itemId}">${icon} ${escape(file.name)}</span>
        </li>`;

        // Show folder contents if loaded
        if (folderContents && folderContents.length > 0) {
          html += `<li class="${styles.subFolderContent}">`;
          folderContents.forEach(subFile => {
            const subIcon = subFile.type === 'folder' ? '' : '';
            html += `<div class="${styles.subFileItem}">
              <input type="checkbox" data-file-id="${subFile.id}" />
              <span>${subIcon} ${escape(subFile.name)}</span>
            </div>`;
          });
          html += `</li>`;
        }
      } else {
        html += `<li class="${styles.fileItem}">
          <input type="checkbox" data-file-id="${file.id}" />
          <span>${icon} ${escape(file.name)}</span>
        </li>`;
      }
    });
    html += '</ul>';
    return html;
  }

  private async _loadFolderContents(driveId: string, itemId: string, targetElement: HTMLElement): Promise<void> {
    try {
      if (!this._teamsService) {
        const graphClient = await this.context.msGraphClientFactory.getClient('3');
        this._teamsService = new TeamsService(graphClient);
      }

      // Find the parent container and store folder contents there
      const folderId = `folder_${itemId}`;

      // Check if already loaded - toggle off
      if (this._channelFiles[folderId]) {
        delete this._channelFiles[folderId];
        this.render();
        return;
      }

      const folderItems = await this._teamsService.getDriveItemChildren(driveId, itemId);

      // Store folder contents and re-render
      this._channelFiles[folderId] = folderItems.map(item => ({
        id: item.id,
        name: item.name,
        type: item.folder ? 'folder' : 'file',
        driveId: driveId,
        itemId: item.id
      }));

      this.render();
    } catch (error) {
      console.error('Failed to load folder contents:', error);
    }
  }

  protected onInit(): Promise<void> {
    return this._getEnvironmentMessage().then(message => {
      this._environmentMessage = message;
    });
  }



  private _getEnvironmentMessage(): Promise<string> {
    if (!!this.context.sdks.microsoftTeams) { // running in Teams, office.com or Outlook
      return this.context.sdks.microsoftTeams.teamsJs.app.getContext()
        .then(context => {
          let environmentMessage: string = '';
          switch (context.app.host.name) {
            case 'Office': // running in Office
              environmentMessage = this.context.isServedFromLocalhost ? strings.AppLocalEnvironmentOffice : strings.AppOfficeEnvironment;
              break;
            case 'Outlook': // running in Outlook
              environmentMessage = this.context.isServedFromLocalhost ? strings.AppLocalEnvironmentOutlook : strings.AppOutlookEnvironment;
              break;
            case 'Teams': // running in Teams
            case 'TeamsModern':
              environmentMessage = this.context.isServedFromLocalhost ? strings.AppLocalEnvironmentTeams : strings.AppTeamsTabEnvironment;
              break;
            default:
              environmentMessage = strings.UnknownEnvironment;
          }

          return environmentMessage;
        });
    }

    return Promise.resolve(this.context.isServedFromLocalhost ? strings.AppLocalEnvironmentSharePoint : strings.AppSharePointEnvironment);
  }

  protected onThemeChanged(currentTheme: IReadonlyTheme | undefined): void {
    if (!currentTheme) {
      return;
    }

    this._isDarkTheme = !!currentTheme.isInverted;
    const {
      semanticColors
    } = currentTheme;

    if (semanticColors) {
      this.domElement.style.setProperty('--bodyText', semanticColors.bodyText || null);
      this.domElement.style.setProperty('--link', semanticColors.link || null);
      this.domElement.style.setProperty('--linkHovered', semanticColors.linkHovered || null);
    }

  }

  protected get dataVersion(): Version {
    return Version.parse('1.0');
  }

  protected getPropertyPaneConfiguration(): IPropertyPaneConfiguration {
    return {
      pages: [
        {
          header: {
            description: strings.PropertyPaneDescription
          },
          groups: [
            {
              groupName: strings.BasicGroupName,
              groupFields: [
                PropertyPaneTextField('description', {
                  label: strings.DescriptionFieldLabel
                })
              ]
            }
          ]
        }
      ]
    };
  }
}
